const statusEnum = {
  pending: 1,
  scheduled: 2,
  canceled: 3,
  declined: 4
}

export default {
  sortAppointments (appointments) {
    appointments.sort((a, b) => {
      if (statusEnum[a.status] > statusEnum[b.status]) return 1
      if (statusEnum[a.status] < statusEnum[b.status]) return -1
      if (a.date > b.date) return 1
      if (a.date < b.date) return -1

      return 0
    })
  }
}
